
.modal-dialog {
    position: relative;
    z-index: 1;
}
.loader {
    position: absolute;
    z-index: 1000;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(255,255,255,.5);
    display: none !important;

    .load-inner {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }

    &:hover {
        cursor: wait;
    }
}
.modal-loading {
    .loader {
        display: block !important;
    }
}
