
.websites-list {
    .list-group-item {
        position: relative;
        padding-left: 36px;

        .website-locale-flag-icon {
            position: absolute;
            left: 11px;
            top: 17px;
            max-width: 16px;
        }
    }
}
.form-check-input {
    height: 1.5em;
}
.form-switch {
    padding-left: 0;
    margin-right: 10px;

    .form-check-input {
        width: 3em;
        margin-left: 0;
        float: none;

        &:checked {
            background-color: var(--bs-success);
        }
    }
}
